.vs-badge {
    display:inline-flex;
    align-items:center;
    border-radius:$box-border-radius;
    padding:12px 16px;
    border:1.75px solid colour(subtext);
    color:colour(subtext);
    background-color: colour(badge-bg);
    margin-right:8px;
    line-height:1em;
    font-size:13px;
    margin-bottom:8px;
    transition: background-color .2s ease-in-out, color .2s ease-in-out, border-color .2s ease-in-out;
    &--primary {
        background-color:colour(badge-bg-primary);
        color: colour(primary) !important;
        border-color:colour(primary);
    }
    &--small {
        padding:8px 8px;
        font-size:10px;
    }
    &__icon {
        font-size:20px;
        line-height:0;
        margin-right:6px;
        margin-left:-2px;
    }
}